<template>
  <el-card>
    <!--  -->
    <div class="panel-area">
      <ul class="participants">
        <li v-for="item in nicknames" :key="item">
          <span>{{ item }} {{ item === nickname ? '' : '' }}</span>
          <el-tag v-if="item === holder" size="mini"></el-tag>
        </li>
      </ul>
    </div>

    <!--  -->
    <div class="panel-area button-area">
      <el-button
        v-if="!isGameStarted"
        type="primary"
        size="small"
        icon="el-icon-edit"
        @click="startGameHandler"
      ></el-button>

      <el-button
        v-if="isGameStarted && nickname === holder"
        type="warning"
        size="small"
        icon="el-icon-delete"
        @click="stopGameHandler"
      ></el-button>

      <el-button
        v-if="isGameStarted && nickname !== holder"
        type="success"
        size="small"
        icon="el-icon-magic-stick"
        @click="answerGameHandler"
      ></el-button>

      <el-button
        type="danger"
        size="small"
        icon="el-icon-switch-button"
        @click="exitHandler"
      ></el-button>
    </div>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="resultDialogVisible"
      width="30%"
    >
      <el-input v-model="expectImageName" placeholder="" />

      <span slot="footer" class="dialog-footer">
        <el-button @click="resultDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveDialogHandler"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="answerDialogVisible"
      width="30%"
    >
      <el-input v-model="inputImageName" placeholder="" />

      <span slot="footer" class="dialog-footer">
        <el-button @click="answerDialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="saveAnswerDialogHandler"
        > </el-button>
      </span>
    </el-dialog>
  </el-card>
</template>

<script>
import { mapGetters, mapState } from 'vuex'

export default {
  data() {
    return {
      resultDialogVisible: false,
      expectImageName: '',
      answerDialogVisible: false,
      inputImageName: ''
    }
  },

  computed: {
    ...mapState(['nickname', 'nicknames', 'holder']),
    ...mapGetters(['isGameStarted'])
  },

  methods: {
    startGameHandler() {
      // 
      // 1. 
      this.resultDialogVisible = true
      // 2. 
      this.expectImageName = ''
    },

    stopGameHandler() {
      this.$confirm('?', '').then(() => {
        // 
        this.$store.dispatch('sendStopGame')
      }).catch(e => {
        console.log(e)
      })
    },

    answerGameHandler() {
      this.answerDialogVisible = true
      this.inputImageName = ''
    },

    saveDialogHandler() {
      // 1. 
      if (!this.expectImageName) {
        this.$message.error('!')
        return
      }
      // 2. 
      this.$store.dispatch('sendStartGame', this.expectImageName)

      // 3. 
      this.resultDialogVisible = false
    },

    saveAnswerDialogHandler() {
      // 1. 
      if (!this.inputImageName) {
        this.$message.error('')
        return
      }
      // 2. 
      this.$store.dispatch('sendAnswerGame', this.inputImageName)
      // 3. 
      this.answerDialogVisible = false
    },

    exitHandler() {
      this.$confirm('', '').then(() => {
        this.$store.dispatch('sendUserLeave')
        this.$router.replace('/login')
      }).catch(e => {
        console.log(e)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.panel-area {
  margin: 10px 0;
}
</style>
